.aboutContainer {
  flex-wrap: wrap;
  margin: 0px;
}

.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(10, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  background-color: rgba(0, 0, 0, .05);
}

.picDiv {
  grid-area: 1 / 1 / 6 / 2; 
  margin-top: 10px;
}
.pic {
  padding-left: 25px;
  object-fit: contain;
}
.skills { 
  grid-area: 1 / 2 / 6 / 3; 
  margin-left: 30px;
  padding-right: 50px;
  
}
.skillHeadline{
  margin-top: 5px;
}

h2{
  color: black;
  background-color: linen;
}

.talking {
  grid-area: 6 / 1 / 11 / 3;
  display:flex;
  flex-wrap: wrap;
  font-size: 25px;
  color: white;
  padding-left: 25px;
  padding-right: 50px;
}


ul {
  list-style: none; 
  font-size: larger;
}

ul li::before {
  content: "\2022"; 
  color: white; 
  font-weight: bold;
  font-size: larger;
  display: inline-block; 
}

